import $ from 'jquery'
import M from 'materialize-css'
import './kjvbot.css';

var jQuery = $;
var kjvBot = {
  'options': (localStorage.getItem('kjvBot')) ? JSON.parse(localStorage.kjvBot) : {},
  'results': [],
  'data': {
    'books': ["Genesis", "Exodus", "Leviticus", "Numbers", "Deuteronomy", "Joshua", "Judges", "Ruth", "1 Samuel", "2 Samuel", "1 Kings", "2 Kings", "1 Chronicles", "2 Chronicles", "Ezra", "Nehemiah", "Esther", "Job", "Psalms", "Proverbs", "Ecclesiastes", "Song of Solomon", "Isaiah", "Jeremiah", "Lamentations", "Ezekiel", "Daniel", "Hosea", "Joel", "Amos", "Obadiah", "Jonah", "Micah", "Nahum", "Habakkuk", "Zephaniah", "Haggai", "Zechariah", "Malachi", "Matthew", "Mark", "Luke", "John", "Acts", "Romans", "1 Corinthians", "2 Corinthians", "Galatians", "Ephesians", "Philippians", "Colossians", "1 Thessalonians", "2 Thessalonians", "1 Timothy", "2 Timothy", "Titus", "Philemon", "Hebrews", "James", "1 Peter", "2 Peter", "1 John", "2 John", "3 John", "Jude", "Revelation"],
    'youtubeIds': $.getJSON('https://virtualimmortal.github.io/kjvbot/data/bibleProjectYoutubeIds.json', function(json) 
    {
      kjvBot.data.youtubeIds = json; // Youtube
    }),
    'verses': $.getJSON('https://virtualimmortal.github.io/kjvbot/data/kjv-verses.json', function(json) 
    {
      kjvBot.data.verses = json;  // Verses
      $('#loadingOverlay').fadeOut(600, function() { $(this).css('display', 'none')});
      $('#kjvBot').focus();
    })
  }
};


/**
 * Initialize page components
 */
document.addEventListener('DOMContentLoaded', function() 
{
  if (kjvBot.data.verses.length) {
    $('#loadingOverlay').fadeOut(600, function() { $(this).css('display', 'none')});
    $('#kjvBot').focus();
  }
  /**
   * Ctrl+f - Search
   */
  $(window).bind('keydown', function(e) {
    if (e.ctrlKey || e.metaKey) {
      switch (String.fromCharCode(e.which).toLowerCase()) {
        case 'f':
          e.preventDefault();
          window.scrollTo(0,0);
          $('#kjvBot').select().focus();
          break;
      }
    }
    return;
  });

  /**
   * Floating Action Menu
   */
  var sidenavs = document.querySelectorAll('.sidenav');
  var sidenav = M.Sidenav.init(sidenavs, {});
console.log(sidenavs);
console.log(sidenav);
  var elems = document.querySelectorAll('.fixed-action-btn');
  var fixedActionButtons = M.FloatingActionButton.init(elems, {
    'hoverEnabled': false,
  });

  $('#floatingActionMenu .searchBtn').on('click', function(e) {
    e.preventDefault();
    window.scrollTo(0,0);
    $('#kjvBot').select().focus();
    fixedActionButtons[0].close();
    return false;
  })

  $('.toTopBtn').on('click', function(e) {
    e.preventDefault();
    window.scrollTo(0,0);
    fixedActionButtons[0].close();
    return false;
  })

  $('.toBottomBtn').on('click', function(e) {
    e.preventDefault();
    window.scrollTo(0,document.body.scrollHeight);
    fixedActionButtons[0].close();
    return false;
  })

});

kjvBot.saveOptions = function() 
{
  localStorage.setItem('kjvBot', JSON.stringify(kjvBot.options));
}

/**
 * Search and sort results in to alphabetical array
 */
kjvBot.search = function(term) {
  var i;
  var classname;
  var sub;
  var parent = this;

  kjvBot.results = [];


  $.each( kjvBot.data.verses, function( key, verse ) {
    var v = verse.verse;
    var regex = new RegExp( term, 'ig' );
    if (v[4].match(regex))
    {
      var friendlyVid = kjvBot.friendlyVid(v);
      //var markup = v[4].replace(regex, '<span class="hilight">'+regex+'</span>');
      var markup = v[4].replace(new RegExp(term, "gi"), (match) => `<mark>${match}</mark>`);
      var verseId = ('00'+v[0]).slice(-8);
      kjvBot.results.push({
        'verseId': v[0]+'-'+v[1]+'-'+v[2]+'-'+v[3],
        'friendlyVid': friendlyVid,
        'markup': markup,
      })
    }
     
  });

  //kjvBot.results.sort((a,b) => (a.name > b.name) ? 1 : ((b.name > a.name) ? -1 : 0))

  this.showSearchResults();
}


/**
 * Display search results
 */
kjvBot.showSearchResults = function() 
{
  var markup = this.results.map(
  (result) => `
  <div class="verse-result-container panel">
    <a class="btn btn-small btn-minimize waves-effect right"><i class="fa fa-times-circle"></i></a>
    <div href="#!" class="verse-result" data-key="${result.verseId}">
      <div class="video-container"></div>
      <div class="markup">${result.markup}</div>
      <div class="reference">${result.friendlyVid}</div>
    </div>
    <div class="verse-nav">
      <a class="btn btn-copy waves-effect"><i class="fa fa-clone"></i></a>
      <a class="btn btn-youtube waves-effect"><i class="fab fa-youtube"></i></a>
      <a class="btn btn-share waves-effect"><i class="material-icons">share</i></a>
    </div>
  </div>
  `);

  // put the markup of the results on the page
  $('#results').html('');
  $('#results').append(markup);
  $('#count').html('('+this.results.length + ' results found)');

  $('.verse-result').on('click', function() 
  {
    var $parent = $(this).closest('.verse-result-container');
    if ($parent.hasClass('expanded')) return;
    
    $parent.toggleClass('expanded');

     return false;
  });

  $('.verse-result-container .btn-minimize').on('click', function() {
    var $parent = $(this).closest('.verse-result-container');
    $parent.removeClass('expanded');
  });

  $('.verse-nav .btn-copy').on('click', function() 
  {
    var $parent = $(this).closest('.verse-result-container').find('.verse-result');
    var verse = $('.markup',$parent).text();
    var reference = $('.reference',$parent).text();
    copyText(verse+"\n"+reference);
    notify('<div style="text-align: center; font-size: 1.2em; padding: 1em;">Copied </div><div >'+$parent.html()+'</div>');

     return false;
  });

  $('.verse-nav .btn-youtube').each(function() {
    $(this).on('click', function() 
    {
      var $parent = $(this).closest('.verse-result-container').find('.verse-result');
      var $videoContainer = $parent.find('.video-container');
      var key = $parent.attr('data-key').split('-');
      var book = kjvBot.data.books[key[1]-1];
      var markup = '';
      if ($videoContainer.find('iframe').length) {
        if ($videoContainer.find('iframe').is(':visible')) { $videoContainer.hide()} else  {$videoContainer.show()}
        return;
      }
      if (kjvBot.data.youtubeIds[book].length > 1) {
        var youtubeIds = kjvBot.data.youtubeIds[book].join(',');
        markup = `
          <iframe src="https://www.youtube.com/embed?listType=playlist&playlist=${youtubeIds}" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
        `;
      
      } 
      else 
      {
        var youtubeId = kjvBot.data.youtubeIds[book][0];
        markup = `
        <iframe src="https://www.youtube.com/embed/${youtubeId}?rel=0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
        `;
      }
      $videoContainer.html(markup).show();
    
      return false;
    });
  });

  $('.verse-nav .btn-share').on('click', function() 
  {
    var $shareDialog = $('#shareDialog');
    $('#dialogs').show();
    $shareDialog.show();

    return false;
  });

  $('body').removeClass('loading');
  
  if (kjvBot.results.length) $('#kjvBot').blur();
  
}


kjvBot.friendlyVid = function(verse) {
  return this.data.books[verse[1]-1]+' '+verse[2]+':'+verse[3];
}

function copyText(text) 
{
   var input = document.createElement('textarea');
   document.body.appendChild(input);
   input.value = text;
   input.select();
   document.execCommand('copy',false);
   input.remove();
}


function notify(message)
{
   M.toast({
      html: message,
      displayLength: 5000
   });
}


export default kjvBot
